import { App, defineAsyncComponent } from "vue";
import { container } from "../../../domain/core/diContainer";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IDialog, IDialogOptions } from "../../../domain/presentation/IDialog";
import { IZindex } from "../../../domain/presentation/IZindex";

interface IDialogData {
    dialog: App<Element>;
    root: HTMLDivElement;
}
const dialogVue = defineAsyncComponent(() => import("../components/ui/dialog.vue"));
container.resolve<IZindex>(IZindex).register(IDialog, { from: 105, step: 2 });

export class DialogImpl implements IDialog {
    private static idCounter = 0;
    private dialogDict: Record<string, IDialogData> = {};
    constructor(
        private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
    ) { }

    public showDialog<ComponentParams>(component: object, options: IDialogOptions = {}, parameters?: ComponentParams) {
        const id = (DialogImpl.idCounter++).toString();
        const root = createDiv({ class: "root" });
        const dialog = this.compCreator.instantiateComponent(dialogVue, { component, options, id, parameters }, root) as IDialogData["dialog"];
        this.dialogDict[id] = { dialog, root };
        document.body.appendChild(root);
        return id.toString();
    }

    public closeDialog(id: string) {
        const dialogData = this.dialogDict[id];
        dialogData.dialog.unmount();
        document.body.removeChild(dialogData.root);
        delete this.dialogDict[id];
    }

    public closeAllDialogs() {
        const ids = Object.keys(this.dialogDict);
        ids.forEach(id => this.closeDialog(id));
    }
}
function createDiv(options: { class?: string }) {
    const div = document.createElement("div");
    if (options.class) {
        div.classList.add("q-dialog-" + options.class);
    }
    return div;
}