import { defineAsyncComponent } from "vue";
import { POSITION, TYPE, useToast } from "vue-toastification";
import { ToastOptions } from "vue-toastification/dist/types/types";
import { createPromiseData } from "../../../common/promiseHelper";
import { toUpperCase } from "../../../common/stringType";
import { container, inject, injectable } from "../../../domain/core/diContainer";
import { IConfirmOptions, IConfirmRetval, INotificationInteractor, INotificationOptions } from "../../../domain/model/INotificationOptions";
import { IConfirmationDialogProps, IConfirmationDialogReturnValue } from "../../../domain/presentation/dialogs/IConfirmationDialogProps";
import { IDialog } from "../../../domain/presentation/IDialog";
import { ILoading } from "../../../domain/presentation/ILoading";
import { INotification } from "../../../domain/presentation/INotification";
import { IZindex } from "../../../domain/presentation/IZindex";

const toast = useToast();
const zindex = container.resolve<IZindex>(IZindex);
zindex.register(INotification, { from: 70 });

@injectable()
export class NotificationImpl implements INotification {
    constructor(@inject(ILoading) private readonly loading: ILoading) {}

    public async showNotification(options: INotificationOptions) {
        const type = TYPE[toUpperCase(options.type)];
        const zindexValue = zindex.getZIndex(INotification);
        const onClose = () => {
            zindex.removeZIndex(INotification, zindexValue);
        };
        let opt: ToastOptions = {
            type,
            closeOnClick: true,
            timeout: options.timeout,
            pauseOnFocusLoss: options.pauseOnFocusLoss || false,
            pauseOnHover: options.pauseOnHover,
            onClose,
            icon: false,
            closeButton: false,
            position: POSITION.BOTTOM_RIGHT,
        };
        if (options.interactors?.find((interactor) => interactor.required)) {
            opt = {
                closeButton: false,
                closeOnClick: false,
                draggable: false,
                timeout: options.timeout || false,
                position: POSITION.TOP_CENTER,
                type,
                pauseOnFocusLoss: options.pauseOnFocusLoss || false,
                pauseOnHover: options.pauseOnHover,
            };
        }
        toast({ component: (await import("../components/notification.vue")).default, props: { options, zindex: zindexValue } }, opt);
    }

    public async showConfirm(options: IConfirmOptions) {
        const prom = createPromiseData<IConfirmRetval>();
        const props: IConfirmationDialogProps = {
            approveText: options.approveText,
            approveIcon: options.approveIcon,
            secondaryApproveText: options.secondaryApproveText,
            cancelText: options.cancelText,
            rejectText: options.rejectText,
            text: options.text,
            timeout: options.timeout,
            customStyle: {
                approve: options.customStyle?.approve,
                secondaryApprove : options.customStyle?.secondaryApprove
            },
            callback: (action: IConfirmationDialogReturnValue) => {
                prom.resolver({
                    approved: action == "approve",
                    result: action,
                });
            },
        };

        await this.loading.stash(async () => {
            const dialog = container.resolve<IDialog>(IDialog);
            dialog.showDialog(
                defineAsyncComponent(() => import(`../components/dialogs/confirmationDialog.vue`)),
                {
                    closable: options.showCloseIcon,
                    title: options.title || "Confirmation",
                    width: options.width ? options.width : "420px",
                    height: options.height ? options.height : "220px",
                    customStyle :options.customStyle,
                },
                { ...props }
            );
            await prom.promise;
        });

        return prom.promise;
    }
}
