import { App, defineAsyncComponent } from "vue";
import { container } from "../../../domain/core/diContainer";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";
import { IPage, IPageOptions } from "../../../domain/presentation/IPage";
import { IZindex } from "../../../domain/presentation/IZindex";

interface IPageData {
    page: App<Element>;
    root: HTMLDivElement;
}

const pageVue = defineAsyncComponent(() => import("../components/ui/page.vue"));

container.resolve<IZindex>(IZindex).register(IPage, { from: 29, step: 2 });

export class PageImpl implements IPage {
    private static idCounter = 0;
    private dict: Record<string, IPageData> = {};
    constructor(
        private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
    ) { }

    public openPage<ComponentParams>(component: object, options?: IPageOptions | undefined, parameters?: ComponentParams): string {
        const id = (PageImpl.idCounter++).toString();
        const root = createDiv({ class: "root" });
        const page = this.compCreator.instantiateComponent(pageVue, {component, options,id, parameters}, root) as IPageData["page"];

        this.dict[id] = {page, root};
        document.body.appendChild(root);
        return id.toString();
    }

    public closePage(id: string) {
        const data = this.dict[id];
        data.page.unmount();
        document.body.removeChild(data.root);
        delete this.dict[id];
    }
    
}

function createDiv(options: { class?: string }) {
    const div = document.createElement("div");
    if (options.class) {
        div.classList.add("q-page-" + options.class);
    }
    return div;
}