import { App, createApp, defineAsyncComponent, getCurrentInstance, Ref, ref } from "vue";
import { container, inject, injectable } from "../../../domain/core/diContainer";
import { ILoading, ILoadingInfo } from "../../../domain/presentation/ILoading";
import { IZindex } from "../../../domain/presentation/IZindex";

interface ILoadingData {
    loading: App<Element>;
    root: HTMLDivElement;
    counter: number;
    timeoutId?: NodeJS.Timeout;
    props: { appear: Ref<boolean>, zindex: number, info: Ref<ILoadingInfo>, showContinueInBgBtn: Ref<boolean>};
}

import loadingVue from "../components/ui/loading2.vue";
import { IAsyncComponentCreator } from "../../../domain/presentation/IAsyncComponentCreator";

@injectable()
export class LoadingImpl implements ILoading {
    private loadingData: ILoadingData;


    constructor(
        @inject(IZindex) private readonly zindex = container.resolve<IZindex>(IZindex),
        @inject(IAsyncComponentCreator) private readonly compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator),
    ) {
        zindex.register(ILoading, { from: 135 });
        const props = { appear: ref(false), zindex: this.zindex.getZIndex(ILoading), info: ref<ILoadingInfo>({title: "", text: ""}), showContinueInBgBtn: ref(false)};

        const root = createDiv({ class: "q-loading-root" });
        const loading = this.compCreator.instantiateComponent(loadingVue, props, root) as ILoadingData["loading"];
        document.body.appendChild(root);
        this.loadingData = { counter: 0, loading, root, props };
    }
    public showLoading(noContinueInBg?: boolean): void {
        if (this.loadingData.counter++ > 1) { return; }

        this.loadingData.props.appear.value = true;
        if(!noContinueInBg && !this.loadingData.timeoutId) {
            this.loadingData.timeoutId = setTimeout(() => this.loadingData.props.showContinueInBgBtn.value = true, 15000);
        }
    }

    public hideLoading(): void {
        if (--this.loadingData.counter > 0) { return; }

        this.loadingData.props.showContinueInBgBtn.value = false;
        if(this.loadingData.timeoutId) {
            clearTimeout(this.loadingData.timeoutId);
            this.loadingData.timeoutId = undefined;
        } 
        this.loadingData.props.appear.value = false;
    }
    public showInfo(newInfo: ILoadingInfo): void {
        this.loadingData.props.info.value = {title: newInfo.title, text: newInfo.text};;
    }
    public hideInfo(): void {
        this.loadingData.props.info.value = {title: "", text: ""};
    }
    public async stash(cb: () => Promise<void>){      
        const oldAppearValue = this.loadingData.props.appear.value;
        try {
            this.loadingData.props.appear.value = false;
            await cb();
        } finally {
            this.loadingData.props.appear.value = oldAppearValue;
        }    
    }

}

function createDiv(options: { class: string }) {
    const div = document.createElement("div");
    div.classList.add(options.class);
    return div;
}