"<script setup lang=\"ts\">\n\nimport { computed, onMounted, ref, Ref, StyleValue, watch } from 'vue';\nimport { container } from '../../../../domain/core/diContainer';\nimport { IUseCaseExecutorManager } from '../../../../domain/objects/IUseCaseExecutorManager';\n\ninterface IProps {\n    appear: Ref<boolean>;\n    zindex: number;\n    info: {title: string, text: string};\n    showContinueInBgBtn: Ref<boolean>;\n}\n\nconst props = defineProps<IProps>();\nconst appear = ref(true);\nconst showInfo = ref({title: \"\", text: \"\"});\nconst appearAnimate = ref(false);\nconst computedClass = computed(() => ({ appearAnimate: appearAnimate.value }));\nconst containerStyle: StyleValue = { zIndex: props.zindex };\nconst bkockShadowStyle: StyleValue = { zIndex: props.zindex };\nlet animating: NodeJS.Timeout | undefined;\nlet appearStart: number = Date.now();\nwatch(props.appear, (val) => {\n    if (animating) { clearTimeout(animating); }\n    if (val) {\n        appear.value = val;\n        animating = setTimeout(() => appearAnimate.value = val);\n        appearStart = Date.now();\n    } else {\n        appearAnimate.value = val;\n        let animationLeft = Date.now() - appearStart;\n        animationLeft = animationLeft > 1000 ? 1000 : animationLeft; // 1000 -> .blockShadow { transition: time }\n        animating = setTimeout(() => appear.value = val, animationLeft);\n    }\n});\nwatch(props.info, (val) => {\n    if (val) {\n        showInfo.value.title = val.title;\n        showInfo.value.text = val.text;\n    }\n    else {\n        showInfo.value = {title: \"\", text: \"\"}\n    }\n})\nonMounted(() => { \n    appear.value = false; \n    showInfo.value = {title: \"\", text: \"\"};\n });\n\nfunction cancel() {\n    const useCaseExecutorManager = container.resolve<IUseCaseExecutorManager>(IUseCaseExecutorManager);\n    useCaseExecutorManager.abortAll();\n}\n</script>\n<template>\n    <teleport to=\"body\">\n         <div class=\"blockShadow\" v-show=\"appear\" :class=\"computedClass\" :style=\"bkockShadowStyle\"></div>\n         <div class=\"container\" v-show=\"appear\" :class=\"computedClass\" :style=\"containerStyle\">\n            <div class=\"loader\" :style=\"[showInfo.title != '' ? 'margin-top: 230px; height: auto' : '']\">\n                <div class=\"top\"></div>\n                <div class=\"spinner-block\">\n                    <div class=\"block\">\n                        <img style=\"width: 150px; height: 150px\" src=\"../../../../images/loading.gif\" alt=\"studio-logo\" />\n                    </div>      \n                </div>\n                <div v-if=\"props.showContinueInBgBtn.value == true\"  class=\"long-loading-info\">\n                    <p>It takes longer than we expected.</p>\n                    <p>You may wait a bit longer or let it continue in the background</p>\n                    <button id=\"cancelButton\" @click=\"cancel()\">CONTINUE IN THE BACKGROUND</button>\n                </div>\n                <div v-if=\"showInfo.title != ''\" class=\"info-div\">\n                    <div class=\"info-title\">{{ showInfo.title }}</div>\n                    <div class=\"info-text\">\n                        {{ showInfo.text }}\n                        <p>Thanks for your patience.</p>\n                    </div>\n                    \n                </div>\n               <div class=\"bottom\"></div>\n            </div>\n         </div>\n       \n \n    </teleport>\n\n</template>\n<style scoped>\n* {\n    font-family: 'Roboto';\n}\n\n.blockShadow {\n    /* background-color: #000000; */\n    top: 0px;\n    left: 0px;\n    position: fixed;\n    height: 100%;\n    width: 100%;\n    opacity: 0;\n    transition: opacity 1s ease-in-out 0.01s;\n    background: linear-gradient(270deg, rgba(255, 255, 255, 0.6) 0.73%, #FFFFFF 36.49%, #FFFFFF 62.71%, rgba(255, 255, 255, 0.6) 100%);\n}\n\n.blockShadow.appearAnimate {\n    opacity: 1;\n}\n\n.container {\n    position: fixed;\n    left: 0%;\n    top: 50%;\n    transition: left 0.5s ease-in-out, opacity 0.5s ease-in-out;\n    opacity: 0;\n    background-color: red;\n}\n\n.container.appearAnimate {\n    left: 20%;\n    opacity: 1;\n}\n\n.content {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 100%;\n}\n\n/* .loader-wrapper {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    background-color: #242f3f;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.loader {\n    display: inline-block;\n    width: 30px;\n    height: 30px;\n    position: relative;\n    border: 4px solid #Fff;\n    animation: loader 2s infinite ease;\n}\n\n.loader-inner {\n    vertical-align: top;\n    display: inline-block;\n    width: 100%;\n    background-color: #fff;\n    animation: loader-inner 2s infinite ease-in;\n} */\n\n.loader{\n    position: fixed;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    z-index: 1;\n    transition: all 3s ease-out;\n    display: grid;\n    place-content: center;\n}\n.top{\n    top:0\n}\n\n.bottom{\n    bottom: 0;\n}\n.spinner-block{\n    /* position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%,-50%); */\n    display: inline-block;\n    overflow: hidden;\n}\n.block{\n    width: 100%;\n    height: 100%;\n    position: relative;\n    transform: translateZ(0) scale(1);\n    backface-visibility: hidden;\n    transform-origin: 0 0;\n    display: flex;\n    justify-content: center;\n}\n.block div{\n    position: absolute;\n    width: 40px;\n    height: 40px;\n    background: #151233;\n    animation: block 1s linear infinite;\n    box-sizing: content-box;\n}\n\n.block div:nth-child(1){\n    left: 38px;\n    top:38px;\n    animation-delay: 0s;\n\n}\n.block div:nth-child(2){\n    left: 80px;\n    top:38px;\n    animation-delay: 0.125s;\n    \n}\n.block div:nth-child(3){\n    left: 122px;\n    top:38px;\n    animation-delay: 0.25s;\n    \n}\n.block div:nth-child(4){\n    left: 38px;\n    top:80px;\n    animation-delay: 0.875s;\n    \n}\n.block div:nth-child(5){\n    left: 122px;\n    top:80px;\n    animation-delay: 0.375s;\n    \n}\n.block div:nth-child(6){\n    left: 38px;\n    top:122px;\n    animation-delay: 0.75s;\n    \n}\n\n.block div:nth-child(7){\n    left: 80px;\n    top:122px;\n    animation-delay: 0.625s;\n    \n}\n\n.block div:nth-child(8){\n    left: 122px;\n    top:122px;\n    animation-delay: 0.5s;\n    \n}\n.info-div {\n    padding: 0 25%;\n    text-align: center;\n    color: #151233;\n    font-family: 'Montserrat';\n\n}\n.info-title {\n    font-weight: 600;\n    font-size: 30px;\n    line-height: 40px;\n    padding: 0 190px;\n    margin-bottom: 30px;\n}\n.info-text {\n    font-size: 20px;\n    line-height: 30px;\n}\n\n.long-loading-info p {\n    font-size: 24px;\n    line-height: 36px;\n    text-align: center;\n    color: #000000;\n    margin: 0;\n}\n.long-loading-info button {\n    border-radius: 4px;\n    border: 1px solid #151233;\n    color: #151233;\n    background: #FFFFFF;\n    padding: 8px 16px;\n    font-size: 16px;\n    font-weight: 500;\n    margin: auto;\n    display: block;\n    margin-top: 32px;\n}\n\n@keyframes block{\n    0%{\n        background: #E96767;\n    }\n\n    12.5%{\n        background: #E96767;\n    }\n    12.625%{\n        background: #151233;\n    }\n    100%{\n        background: #151233;\n    }\n}\n\n</style>"],"sourceRoot":""}]);
